import { useEffect, useState } from "react";
import ThemeToggle from "./ThemeToggle";
import {
  Link,
  Navigate,
  NavLink,
  useNavigate,
  useLocation,
} from "react-router-dom";
import { useCart } from "./CartContext";

const HeaderOne = () => {
  const [active, setActive] = useState(false);
  const [scroll, setScroll] = useState(false);

  const [activeHeader, setActiveHeader] = useState("Home");
  const [user, setUser] = useState(null);
  

  const [showShopDropdown1, setShowShopDropdown1] = useState(false);
const [showShopDropdown2, setShowShopDropdown2] = useState(false);
  
  const location = useLocation();
  const navigate = useNavigate();

  // Helper function to check if current route matches
  const isActiveRoute = (path) => {
    return location.pathname === path;
  };

  useEffect(() => {
    const storedUser = localStorage.getItem("user");
    const storedCentre = localStorage.getItem("centre")
    if (storedUser) {
      setUser(JSON.parse(storedUser));
    }
     if (storedCentre) {
      setUser(JSON.parse(storedCentre));
    }

  }, []);

  const { cartItems } = useCart(); //to display count of service in cart icon

  useEffect(() => {
    var offCanvasNav = document.getElementById("offcanvas-navigation");
    if (offCanvasNav) {
      var menuExpand = offCanvasNav.querySelectorAll(
        ".has-submenu > .nav-menu__link"
      );
      var numMenuExpand = menuExpand.length;

      function sideMenuExpand() {
        if (this.parentElement.classList.contains("active") === true) {
          this.parentElement.classList.remove("active");
        } else {
          for (let i = 0; i < numMenuExpand; i++) {
            menuExpand[i].parentElement.classList.remove("active");
          }
          this.parentElement.classList.add("active");
        }
      }

      for (let i = 0; i < numMenuExpand; i++) {
        menuExpand[i].addEventListener("click", sideMenuExpand);
      }
    }
  }, []);

  useEffect(() => {
    const handleScroll = () => {
      if (window.pageYOffset < 150) {
        setScroll(false);
      } else {
        setScroll(true);
      }
    };
    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  const mobileMenu = () => {
    setActive(!active);
  };

  const [isOpen, setIsOpen] = useState(false);
  const [showShopDropdown, setShowShopDropdown] = useState(false);

  const handleLogout = () => {
    localStorage.clear();
    setUser(null);
    navigate("/");
  };

  return (
    <>
      <div className="overlay"></div>
      <div className={`side-overlay ${active && "show"}`}></div>
      {/* ==================== Header Start Here ==================== */}
      <header className={`header ${scroll ? "fixed-header" : ""} `}>
        <div className="container container-full">
          <nav className="header-inner flx-between">
            {/* Logo Start */}
            <div className="logo">
              <Link to="/" className="link white-version">
                <img
                  src="/assets/images/logo/BHARATLOGO-011.webp"
                  alt="Logo"
                  style={{ height: "85px", width: "85px" }}
                />
              </Link>
              <Link to="/" className="link dark-version">
                <img src="/assets/images/logo/BHARATLOGO-011.webp" alt="Logo" />
              </Link>
            </div>
            {/* Logo End  */}

            {/* Menu Start  */}
            <div className="header-menu d-lg-block d-none">
              <ul className="nav-menu flx-align">
                <li
                  className={`nav-menu__item ${
                    isActiveRoute("/") ? "activePage" : ""
                  }`}
                >
                  <Link to="/" className="nav-menu__link">
                    Home
                  </Link>
                </li>

                <li
                  className={`nav-menu__item ${
                    isActiveRoute("/all-service") ? "activePage" : ""
                  }`}
                >
                  <Link
                    to="/all-service"
                    state={{ tab: "services" }}
                    className="nav-menu__link"
                  >
                    Health Services
                  </Link>
                </li>

                <li
                  className={`nav-menu__item ${
                    isActiveRoute("/all-package") ? "activePage" : ""
                  }`}
                >
                  <Link
                    to="/all-package"
                    state={{ tab: "packages" }}
                    className="nav-menu__link"
                  >
                    Health Checkup Packages
                  </Link>
                </li>

                <li
                  className={`nav-menu__item ${
                    isActiveRoute("/centers") ? "activePage" : ""
                  }`}
                >
                  <Link to="/centers" className="nav-menu__link">
                    Healthcare Centre
                  </Link>
                </li>

                {/* <li className="nav-menu__item">
                  <Link to="https://blog.4ceps.com" className="nav-menu__link">
                    Blogs
                  </Link>
                </li> */}
                <li
                  className="nav-menu__item has-submenu"
                  onMouseEnter={() => setShowShopDropdown(true)}
                  onMouseLeave={() => setShowShopDropdown(false)}
                >
                  <Link className="nav-menu__link">Shop</Link>
                  {showShopDropdown && (
                    <ul className="nav-submenu">
                      <li>
                        <Link to="/pharmacy" className="nav-submenu__link">
                          Pharmacy
                        </Link>
                      </li>
                      <li>
                        <Link to="/medical-instruments" className="nav-submenu__link">
                          Medical Instruments
                        </Link>
                      </li>
                      <li>
                        <Link to="/health-product" className="nav-submenu__link">
                          Health Products
                        </Link>
                      </li>
                    </ul>
                  )}
                </li>
                <li
                  className={`nav-menu__item ${
                    isActiveRoute("/contact") ? "activePage" : ""
                  }`}
                >
                  <NavLink to="/contact" className="nav-menu__link">
                    Contact
                  </NavLink>
                </li>
              </ul>
            </div>
            {/* Menu End  */}

            {/* Header Right start */}
            <div className="header-right flx-align">
              {/* <div className="auth-buttons d-none d-lg-flex">
                {user ? (
                  <>
                    <button
                      className="btn btn-main pill"
                      style={{ marginRight: "10px" }}
                      onClick={() => navigate("/user-dashboard")}
                    >
                      <span className="icon-left icon">
                        <img src="/assets/images/icons/user.svg" alt="" />
                      </span>
                      {user.name}
                    </button>

                    <div className="header-right__inner gap-3 flx-align">
                      <Link
                        to="/"
                        onClick={handleLogout}
                        className="btn btn-main pill"
                      >
                        <span className="icon-left icon">
                          <img src="/assets/images/icons/user.svg" alt="" />
                        </span>
                        Logout
                      </Link>
                    </div>
                  </>
                ) : (
                  <>
                    <Link
                      to="/login"
                      className="btn btn-main pill"
                      style={{ marginRight: "10px" }}
                    >
                      <span className="icon-left icon">
                        <img src="/assets/images/icons/user.svg" alt="" />
                      </span>
                      Login
                    </Link>

                    <div className="header-right__inner gap-3 flx-align">
                      <Link to="/user-register" className="btn btn-main pill">
                        <span className="icon-left icon">
                          <img src="/assets/images/icons/user.svg" alt="" />
                        </span>
                        Register
                      </Link>
                    </div>
                  </>
                )}
              </div> */}

              <div className="auth-buttons d-none d-lg-flex gap-3">
                 <li type='none'
                  className="nav-menu__item has-submenu"
                  onMouseEnter={() => setShowShopDropdown(true)}
                  onMouseLeave={() => setShowShopDropdown(false)}
                >

                  <Link className="nav-menu__link">{user ? `${user.name.split(' ')[0]}` : 'Login'}</Link>
    
                   {showShopDropdown && (
                    <ul className="nav-submenu">
                      {user ? (
                        <>
                          <li>
                            <Link  to={user.role === "Centre" ? "/centre-dashboard" : "/user-dashboard"}  className="nav-submenu__link">
                              {user.name}
                            </Link>
                          </li>
                          <li>
                            <Link to="/" onClick={handleLogout} className="nav-submenu__link">
                              Logout
                            </Link>
                          </li>
                        </>
                      ) : (
                        <>
                          <li>
                            <Link to="/user-dashboard" className="nav-submenu__link">
                              Patient
                            </Link>
                          </li>
                          <li>
                            <Link to="/centre-dashboard" className="nav-submenu__link">
                              Healthcare Centre
                            </Link>
                          </li>
                        </>
                      )}
                    </ul>
                  )}
                </li>

                   <div className="header-right__inner gap-3 flx-align">
                      <Link to="/user-register" className="btn btn-main pill">
                        <span className="icon-left icon">
                          <img src="/assets/images/icons/user.svg" alt="" />
                        </span>
                        Register
                      </Link>
                    </div>
              </div>

              <Link
                to="/cart"
                className="header-right__button cart-btn position-relative"
              >
                <img
                  src="/assets/images/icons/cart.svg"
                  alt=""
                  className="white-version"
                />
                <img
                  src="/assets/images/icons/cart-white.svg"
                  alt=""
                  className="dark-version"
                />
                <span className="qty-badge font-12">{cartItems.length}</span>
              </Link>

              <button
                type="button"
                className="toggle-mobileMenu d-lg-none"
                onClick={mobileMenu}
              >
                <i className="fas fa-bars" style={{ color: "#777",fontSize:"28px" }} />
              </button>
            </div>
            {/* Header Right End  */}
          </nav>
        </div>
      </header>
      {/* ==================== Header End Here ==================== */}

      <div className={`mobile-menu d-lg-none d-block ${active && "active"}`}>
        <button
          type="button"
          className="close-button text-body hover-text-main"
          onClick={mobileMenu}
        >
          <i className="fas fa-times" style={{ color: "#777",fontSize:"16px" }} />
        </button>

        <div className="mobile-menu__inner">
          <Link to="/" className="mobile-menu__logo">
            <img
              src="/assets/images/logo/BHARATLOGO-01.png"
              alt="Logo"
              style={{ height: "80px", width: "80px" }}
              className="white-version"
            />
            <img
              src="assets/images/logo/white-logo-two.png"
              alt="Logo"
              className="dark-version"
            />
          </Link>
          <div className="mobile-menu__menu" style={{ marginTop: "-20px" }}>
            <ul
              className="nav-menu flx-align nav-menu--mobile"
              id="offcanvas-navigation"
            >
              <li
                className={`nav-menu__item ${
                  isActiveRoute("/") ? "activePage" : ""
                }`}
              >
                <Link to="/" className="nav-menu__link">
                  Home
                </Link>
              </li>

              <li
                className={`nav-menu__item ${
                  isActiveRoute("/all-service") ? "activePage" : ""
                }`}
              >
                <Link
                  to="/all-service"
                  state={{ tab: "services" }}
                  className="nav-menu__link"
                >
                 Health Services
                </Link>
              </li>

              <li
                className={`nav-menu__item ${
                  isActiveRoute("/all-package") ? "activePage" : ""
                }`}
              >
                <Link
                  to="/all-package"
                  state={{ tab: "packages" }}
                  className="nav-menu__link"
                >
                  Health Checkup Packages
                </Link>
              </li>

              <li
                className={`nav-menu__item ${
                  isActiveRoute("/centers") ? "activePage" : ""
                }`}
              >
                <Link to="/centers" className="nav-menu__link">
                  HealthCare Center
                </Link>
              </li>

              {/* <li className="nav-menu__item">
                <Link to="https://blog.4ceps.com" className="nav-menu__link">
                  Blogs
                </Link>
              </li> */}
             <li 
  className="nav-menu__item has-submenu"
  onMouseEnter={() => setShowShopDropdown1(true)}
  onMouseLeave={() => setShowShopDropdown2(false)}
>
  <Link className="nav-menu__link">Shop</Link>
  {showShopDropdown1 && (
    <ul className="nav-submenu">
      <li><Link to="/pharmacy" className="nav-submenu__link">Pharmacy</Link></li>
      <li><Link to="/medical-instruments" className="nav-submenu__link">Medical Instruments</Link></li>
      <li><Link to="/health-product" className="nav-submenu__link">Health Products</Link></li>
    </ul>
  )}
</li>

              <li
                className={`nav-menu__item ${
                  isActiveRoute("/contact") ? "activePage" : ""
                }`}
              >
                <NavLink to="/contact" className="nav-menu__link">
                  Contact
                </NavLink>
              </li>

 <li 
  className="nav-menu__item has-submenu"
  onMouseEnter={() => setShowShopDropdown2(true)}
  onMouseLeave={() => setShowShopDropdown1(false)}
>
  <Link className="nav-menu__link">{user ? `${user.name.split(' ')[0]}` : 'Login'}</Link>
  {showShopDropdown2 && (
                    <ul className="nav-submenu">
                      {user ? (
                        <>
                          <li>
                            <Link  to={user.role === "Centre" ? "/centre-dashboard" : "/user-dashboard"}  className="nav-submenu__link">
                              {user.name}
                            </Link>
                          </li>
                          <li>
                            <Link to="/" onClick={handleLogout} className="nav-submenu__link">
                              Logout
                            </Link>
                          </li>
                        </>
                      ) : (
                        <>
                          <li>
                            <Link to="/user-dashboard" className="nav-submenu__link">
                              Patient
                            </Link>
                          </li>
                          <li>
                            <Link to="/centre-dashboard" className="nav-submenu__link">
                              Healthcare Centre
                            </Link>
                          </li>
                        </>
                      )}
                    </ul>
                  )}
</li>
              

              
              
            </ul>

            {/* <div className="header-right__inner d-lg-none my-3 gap-2 d-flex flx-align">
              {user ? (
                <>
                  <button
                    className="btn btn-main pill"
                    onClick={() => navigate("/user-dashboard")}
                  >
                    <span className="icon-left icon">
                      <img src="assets/images/icons/user.svg" alt="" />
                    </span>
                    {user.name}
                  </button>

                  <div className="header-right__inner gap-3 flx-align">
                    <Link
                      to="/"
                      onClick={handleLogout}
                      className="btn btn-main pill"
                    >
                      <span className="icon-left icon">
                        <img src="/assets/images/icons/user.svg" alt="" />
                      </span>
                      Logout
                    </Link>
                  </div>
                </>
              ) : (
                <>
                  <Link
                    to="/login"
                    className="btn btn-main pill"
                    style={{ marginRight: "10px" }}
                  >
                    <span className="icon-left icon">
                      <img src="/assets/images/icons/user.svg" alt="" />
                    </span>
                    Login
                  </Link>

                  <div className="header-right__inner gap-3 flx-align">
                    <Link to="/user-register" className="btn btn-main pill">
                      <span className="icon-left icon">
                        <img src="/assets/images/icons/user.svg" alt="" />
                      </span>
                      Register
                    </Link>
                  </div>
                </>
              )}
              {/* <Link to="/login" className="btn btn-main pill">
                <span className="icon-left icon">
                  <img src="assets/images/icons/user.svg" alt="" />
                </span>
                Login
              </Link>
              <Link to="/user-register" className="btn btn-main pill">
                <span className="icon-left icon">
                  <img src="assets/images/icons/user.svg" alt="" />
                </span>
                Register
              </Link> */}
            {/* </div>  */}
            
            <div className="header-right__inner d-lg-none my-3 gap-2 d-flex flx-align">
               <Link to="/user-register" className="btn btn-main pill">
                <span className="icon-left icon">
                  <img src="assets/images/icons/user.svg" alt="" />
                </span>
                Register
              </Link> 
            </div> 
            
          </div>
        </div>
        <style>
          {`

          
@media (max-width: 576px) {
  .header {
    padding: 0px 10px !important;
    height: 95px !important;
  }
   

  .header .logo img {
    height: 60px !important;
    width: 60px !important;
  }

  .header-right .btn {
    padding: 4px 10px !important;
    font-size: 13px !important;
  }

  .toggle-mobileMenu {
    font-size: 20px !important;
  }
}

`}
        </style>
      </div>
    </>
  );
};

export default HeaderOne;
