import { Link } from "react-router-dom";

const FooterOne = () => {
  return (
    <>
      <footer className="footer-section">
        <img
          src="/assets/images/shapes/pattern.png"
          alt=""
          className="bg-pattern"
        />

        <img
          src="/assets/images/gradients/footer-gradient1.webp"
          alt=""
          className="bg--gradient"
        />

        <div className="container container-two" style={{paddingTop:"22px"}}>
          <div className="row gy-4 gy-lg-5" style={{justifyContent:"center"}}>
            {/* Left Content - Logo and Description */}
            <div className="col-xl-4 col-lg-4 col-md-6 col-sm-12 col-12 ftrCol">
              <div className="footer-widget">
                <div className="footer-widget__logo">
                  <Link to="/" >
                    <img src="/assets/images/logo/BHARATLOGO-011.webp" alt=""  style={{width:"auto"}} className="footer-logo"/>
                  </Link>
                </div>

                <p className="footer-widget__desc">
                  Your trusted partner for easy access to verified healthcare services, appointments, and diagnostics — anytime, anywhere.
                </p>
              </div>
            </div>

            {/* Patient Column */}
            <div className="col-xl-3 col-lg-3 col-md-6 col-sm-4 col-6 ps-xl-5 ftrCol footer-links-col">
              <div className="footer-widget">
                <h5 className="footer-widget__title text-white">Patient</h5>
                <ul className="footer-lists">
                  <li className="footer-lists__item">
                    <Link to="/login" className="footer-lists__link">
                      Login
                    </Link>
                  </li>

                  <li className="footer-lists__item">
                    <Link to="/user-register" className="footer-lists__link">
                      Register
                    </Link>
                  </li>

                  <li className="footer-lists__item">
                    <Link
                      to="/all-service" state={{tab:"services"}}
                      className="footer-lists__link"
                    >
                      Services
                    </Link>
                  </li>

                  <li className="footer-lists__item">
                    <Link
                      to="/all-package" state={{tab:"packages"}}
                      className="footer-lists__link" 
                    >
                      Health Checkup Packages
                    </Link>
                  </li>
                </ul>
              </div>
            </div>

            {/* Centre Column */}
            <div className="col-xl-3 col-lg-3 col-md-6 col-sm-4 col-6 ftrCol footer-links-col">
              <div className="footer-widget">
                <h5 className="footer-widget__title text-white">Healthcare Centre</h5>
                <ul className="footer-lists">
                  <li className="footer-lists__item">
                    <Link to="/centre-login" className="footer-lists__link">
                      Login
                    </Link>
                  </li>

                  <li className="footer-lists__item">
                    <Link
                      to="/centre-register" state={{tab:"services"}}
                      className="footer-lists__link"
                    >
                      Register
                    </Link>
                  </li>
                  <li className="footer-lists__item">
                    <Link
                      to="/centre-dashboard" state={{tab:"services"}}
                      className="footer-lists__link"
                    >
                      Dashboard
                    </Link>
                  </li>
                </ul>
              </div>
            </div>

            {/* About 4ceps Column */}
            <div className="col-xl-2 col-lg-2 col-md-6 col-sm-4 col-12 ftrCol footer-links-col about-col">
              <div className="footer-widget">
                <h5 className="footer-widget__title text-white">About 4ceps</h5>
                <ul className="footer-lists">
                  <li className="footer-lists__item">
                    <Link to="/" className="footer-lists__link">
                      Home
                    </Link>
                  </li>

                  <li className="footer-lists__item">
                    <Link to="https://blog.4ceps.com" className="footer-lists__link">
                      Blogs
                    </Link>
                  </li>

                  <li className="footer-lists__item">
                    <Link to="/contact" className="footer-lists__link">
                      Contact
                    </Link>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </footer>

      {/* bottom Footer */}
      <div className="bottom-footer">
        <div className="container container-two">
          <div className="bottom-footer__inner flx-between gap-3 flex-column flex-md-row">
            <p className="bottom-footer__text font-14 mb-2 mb-md-0">
              Copyright © {new Date().getFullYear()}{" "} <a
    href="https://www.4ceps.com/"
    target="_blank"
    className="footer-link font-14 underline-on-hover"
  >4ceps
  </a>, All rights reserved.
            </p>
            <div className="footer-links d-flex flex-column flex-md-row gap-2 gap-md-3">
              <Link to="/terms-booking" target="_blank" className="footer-link font-14">
                Terms and conditions
              </Link>

              <Link to="/privacy-booking" target="_blank" className="footer-link font-14">
                Privacy Policy
              </Link>

               <Link to="/payment-policy"  target="_blank" className="footer-link font-14">
                Payment Policy
              </Link>
            </div>



    <div className="footer-links d-flex flex-column flex-md-row gap-md-2">
  <span className="bottom-footer__text font-14 mb-2 mb-md-0">Designed and Developed by</span>
  <a
    href="https://www.metromindz.com/"
    target="_blank"
    className="footer-link font-14 underline-on-hover"
  >Metromindz
  </a>
</div>


          </div>
        </div>
      </div>

      {/* Custom CSS for responsive design */}
      <style jsx>{`
        /* Large screens - center alignment */

        .footer-link:hover .underline-on-hover {
  text-decoration: underline;
}
        @media (min-width: 992px) {
          .footer-links-col {
            display: flex;
            flex-direction: column;
            align-items: center;
            line-height: 1.3;
          }
        }

        /* Medium screens */
        @media (min-width: 768px) and (max-width: 991.98px) {
          .footer-links-col {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            line-height:1;
          }

          .container {

padding:20px
 padding-left:30px;
  flex-wrap: wrap;           
  gap: 15px;               
 
}

        }

        /* Small screens - Centre and About 4ceps in same row */
        @media (min-width: 576px) and (max-width: 767.98px) {
          .footer-links-col {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            padding-left: 15px !important;
          }
          
          .about-col {
            padding-left: 10px !important;
          }
          
          .footer-widget__title {
            font-size: 16px;
            line-height: 1.3;
            margin-bottom: 12px;
          }
          
          .footer-lists__item {
            margin-bottom: 6px;
            line-height: 1.3;
          }
          
          .footer-lists__link {
            font-size: 14px;
            line-height: 1.3;
          }
          
          .footer-logo {
            height: 85px !important;
            width: 80px !important;
          }
          
          .footer-widget__desc {
            font-size: 14px;
            line-height: 1.4;
          }
        }

        /* Extra small screens */
        @media (max-width: 575.98px) {
          // .container {
          //   padding-top: 35px !important;
          // }
            .bottom-footer__inner{
            padding-top: 0px !important;
            
            }
          
          .footer-links-col {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            padding-left: 15px !important;
          }
          
          .about-col {
            margin-top: 20px;
            padding-left: 15px !important;
          }
          
          .footer-widget__title {
            font-size: 16px;
            line-height: 1.2;
            margin-bottom: 10px;
          }
          
          .footer-lists__item {
            margin-bottom: 4px;
            line-height: 1.2;
          }
          
          .footer-lists__link {
            font-size: 13px;
            line-height: 1.2;
          }
          
          .footer-logo {
            height: 75px !important;
            width: 70px !important;
          }
          
          .footer-widget__desc {
            font-size: 13px;
            line-height: 1.3;
            margin-bottom: 15px;
          }
          
          .row {
            margin-left: -10px;
            margin-right: -10px;
          }
          
          .ftrCol {
            padding-left: 10px;
            padding-right: 10px;
          }
        }

        /* Very small screens - reduce gaps further */
        @media (max-width: 480px) {
          .footer-links-col {
            padding-left: 10px !important;
            line-height:1.5;
          }

            .footer-links-col .footer-widget__title,
  .footer-links-col .footer-lists__item,
  .footer-links-col .footer-lists__link {
    line-height: 1 !important;
  }
          
          
          .about-col {
            padding-left: 10px !important;
          }
          
          .ps-xl-5 {
            padding-left: 10px !important;
          }
        }

        /* Bottom footer responsive */
        @media (max-width: 767.98px) {
          .bottom-footer__inner {
            text-align: center;
          }
          
          .footer-links {
            justify-content: center;
          }
        }
      `}</style>
    </>
  );
};

export default FooterOne;